* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration: none;
}
body {
  text-decoration: none;
}

.ul {
  list-style-type: none;
  text-decoration: none;
  list-style: none;
}
.li {
  list-style: none;
  list-style-type: none;
  text-decoration: none;
}
.a {
  text-decoration: none;
}

.App {
  background: #1e252b;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  max-width: 1220px;
}
.title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 50px;
  line-height: 140%;
  color: rgba(255, 255, 255, 0.9);
}
.logo_title {
  color: rgba(255, 255, 255, 0.9);
}

.description {
  margin-bottom: 60px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: rgba(255, 255, 255, 0.55);
}
