.tools {
  padding-top: 100px;
  background: #1e252b;

  .title {
    display: flex;
    justify-content: center;
  }

  .description {
    display: flex;
    justify-content: center;
  }

  &__load {
    margin-left: 50%;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    color: #ff6e30;
    background: #1e252b;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    padding: 17px 38px;
    transition: 0.3s;

    &:hover {
      transition: 0.3s;
      cursor: pointer;
      color: #1e252b;
      background: #ff6e30;
    }
  }

  .tool__wrap {
    padding-top: 100px;
    padding-bottom: 100px;
    display: grid;
    grid-template-columns: 32% 32% 32%;
    gap: 25px;
  }

  @media (max-width: 1145px) {
    .tool__wrap {
      grid-template-columns: 40% 40%;
    }

    .tools__load {
      margin-left: 29%;
    }
  }

  @media (max-width: 927px) {
    .tool__wrap {
        grid-template-columns: 40% 40%;
      }
    }

  @media (max-width: 772px) {
    .tool__wrap {
        padding-top: 40px;
        padding-bottom: 70px;
        grid-template-columns: 70% ;
    }
    .title {
        font-size: 40px;
    }

    .description {
        font-size: 14px;
    }
  }

  @media (max-width: 500px){
    .tool__wrap {
        padding-top: 40px;
        padding-bottom: 70px;
        grid-template-columns: 90% ;
    }
  }
}
